<template>
  <div class="app-bar">
    <!--<v-system-bar color="primaryDark"></v-system-bar>-->

    <v-app-bar
      color="primaryDark accent-4"
      dark
      permantent
      fixed
    >
      <v-app-bar-nav-icon
        @click="toggleNavigationDrawer"
      ></v-app-bar-nav-icon>

      <v-toolbar-title>Meu Barbeiro</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-menu v-if="userState" right offset-y>
        <template v-slot:activator="{ on, attrs }">
          <v-btn icon v-bind="attrs" v-on="on">
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-item
            v-for="(item, index) in topMenuItems"
            :key="index"
            @click="onTopMenuClicked(item.action)"
          >
            <v-list-item-title>
              {{ item.title }}
            </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>

    <navigation-drawer ref="navigationDrawer" />
  </div>
</template>

<script>
import NavigationDrawer from './NavigationDrawer'
import NavigationMixin from '@/mixins/NavigationMixin'
import UserMixin from '@/mixins/UserMixin'
export default {
  components: { NavigationDrawer },
  mixins: [ NavigationMixin, UserMixin ],
  data: () => ({
    topMenuItems: [
			{ title: 'Meu Perfil', action: 'site-profile' },
			{ title: 'Alterar Senha', action: 'site-change-password' },
			{ title: 'Sair', action: 'logout' }
		]
  }),
  methods: {
    onTopMenuClicked (action) {
      if (action === 'logout') this.logoutUser()
			else if (action === 'site-change-password') this.goTo('site-change-password')
			else if (action === 'site-profile') this.goTo('site-profile')
    },
    toggleNavigationDrawer () {
      this.$refs.navigationDrawer.toggleNavigationDrawer()
    }
  }
}
</script>

<style scoped>
@media all and (min-width: 960px) {
  .app-bar { display: none; }
}
</style>