<template>
  <ul class="navigation-bar">
    <navigation-bar-item
      v-for="(item, index) in itemsCmpt" 
      :active="item.active"
      :key="index"
      :text="item.text"
      :route="item.route"
    />
  </ul>
</template>

<script>
import NavigationBarItem from './NavigationBarItem'
import UserMixin from '@/mixins/UserMixin'
export default {
  components: { NavigationBarItem },
  mixins: [ UserMixin ],
  data: () => ({
    items: [
      { text: 'Agende um horrio', route: 'search' },
      { text: 'Meus agendamentos', route: 'site-schedule' },
      { text: 'Entrar', route: 'login' },
      { text: 'Meus dados', route: 'site-profile' },
      { text: 'Para barbeiros', route: 'barber-auth' }
    ]
  }),
  computed: {
    itemsCmpt () {
      let items = [... this.items]
      const routeNameToBeDelete = (this.userState) ? 'login' : 'site-profile'
      const indexToBeDelete = items.findIndex(item => item.route === routeNameToBeDelete)
      if (indexToBeDelete > -1) items.splice(indexToBeDelete, 1) 
      const routeName = this.$route.meta.navigationBarLink
      return items.map(i => {
        i.active = i.route === routeName
        return i
      })
    }
  }
}
</script>

<style scoped>
.navigation-bar {
  height: 80px;
  display: flex;
  align-content: flex-end;
  justify-content: flex-end;
  padding: 0 !important;
}
</style>